@media screen and (max-width: 1200px) {
  div#body{
      width: 95%;
      margin: 0px auto;
      display: block;
  }

  div.nose{
      width: 95%;
  }

  div.total{
      width: 85%;
  }
}
strong {
font-weight: bold; /* Aplica negrita */
font-size: 20px; /* Tamaño de fuente (ajusta según tus preferencias) */
color: #333; /* Color del texto (ajusta según tus preferencias) */
margin: 100px auto 10px; /* Agrega aún más espacio arriba, centra horizontalmente y espacio abajo */
display: block; /* Convierte el elemento en un bloque para centrarlo */
text-align: center; /* Centra el texto dentro del elemento */
}

